import type { IRestProps } from '../../../../common';

type ModeType = 'singleLine' | 'doubleLine';

export type ISingLineItems = Array<React.ReactElement[] | 'divider'>;

export type DoubleLineGroup = React.ReactElement[] | [ React.ReactElement[],  React.ReactElement[]] | 'divider';
export type IDoubleLineItems = DoubleLineGroup[];

export type IToolbarLayoutProps = IRestProps & {
  /**
   * 工具栏的模式(单行或双行)，非受控模式
   */
  defaultMode?: ModeType;
  /**
   * 工具栏的模式(单行或双行)，受控模式
   */
  mode?: ModeType;
  /**
   * 工具栏模式发生变化时回调
   */
  onModeChange?: (mode: ModeType) => void;
  /**
   * 单行工具栏
   */
  singleLineToolbar?: ISingLineItems;
  /**
   * 双行工具栏
   */
  doubleLineToolbar?: IDoubleLineItems;
  /**
   * 内部元素对齐方式
   */
  align?: 'left' | 'center';
  layout?: 'fix-wrapper' | 'fit-content';
  /**
   * 完成一次布局渲染的回调
   */
  onLayoutRender?: () => void;
}